.errorContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: #ff6060;
  min-height: 70vh;
}

.errorContainer h2 {
  font-size: 288px;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 0;
  height: 350px;
}
.errorContainer p {
  font-size: 36px;
  font-weight: 500;
  margin-top: 0;
  width: 80%;
}
.errorContainer a {
  color: #ff6060;
  margin-bottom: 50px;
  margin-top: 50px;
}
@media only screen and (max-width: 768px) {
  .errorContainer h2 {
    font-size: 96px;
    height: 180px;
  }
  .errorContainer p {
    font-size: 18px;
  }
}
