.AProposContainer {
  padding-left: 40px;
  padding-right: 40px;
}
.DropdownContainer {
  padding-left: 50px;
  padding-right: 50px;
  margin-top: 30px;
  min-height: 55vh;
  margin-bottom: 20px;
}

.DropdownWrapper {
  border-radius: 10px;
  margin-bottom: 35px;
  display: flex;
  flex-direction: column;
  width: 100%;
}
.DropdownHeader {
  background-color: #ff6060;
  border-radius: 10px;
  display: flex;
  align-items: center;
}
.DropdownTitle {
  color: #ffffff;
  height: 40px;
  display: flex;
  align-items: center;
  padding-left: 15px;
  font-size: 24px;
  font-weight: 500;
}
.DropdownHeader a {
  display: flex;
  justify-content: flex-end;
  margin-right: 10px;
  width: 100%;
}

.DropdownContent {
  background-color: #f6f6f6;
  border-radius: 0 0 5px 5px;
  color: #ff6060;
  min-height: 100px;
  padding: 25px 10px 10px 10px;
}
@media only screen and (max-width: 768px) {
  .DropdownContainer {
    padding-left: 0;
    padding-right: 0;
  }
  .DropdownHeader {
    height: 30px;
  }
  .DropdownTitle {
    font-size: 13px;
  }
  .DropdownHeader a img {
    width: 9px;
    height: 15px;
  }
  .DropdownContent {
    font-size: 12px;
    font-weight: 400;
    min-height: 70px;
  }
}
